#nav_bar {
  display: none;
  width: 25px;
  height: 17px;
  padding: 0 12px;
  position: relative;
  vertical-align: middle;
}
#nav_bar:hover {
  cursor: pointer;
}
#nav_bar span {
  position: relative;
  display: block;
  width: inherit;
  height: inherit;
}
#nav_bar:after,
#nav_bar span:before,
#nav_bar span:after {
  content: "";
  position: absolute;
  display: block;
  width: 25px;
  height: 3px;
  border-radius: 0.2rem;
  background-color: #fff;
  left: 0;
  top: calc( 50% + 5.5px);
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}
#nav_bar:after {
  top: 0;
  left: 12px;
}
#nav_bar span:before {
  top: calc( 50% - 1.5px);
}
#nav_bar span:after {
  bottom: 28%;
}
#nav_bar:hover:after {
  top: 0;
}
#nav_bar:hover span:after {
  bottom: 35%;
}
.overflow #nav_bar:after {
  top: calc(50% - 3px);
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}
.overflow #nav_bar span:before {
  opacity: 0;
}
.overflow #nav_bar span:after {
  top: calc(50% - 3px);
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg);
}
